.Icon-container{
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color:#ccc; */
}
.Icon-container-check{
    position: relative;
    left: 12px;
}
.Icon-container-check--active{
    color: #6fda73;
}

.Icon-container-delete:hover{
    color: red
}
.Icon-svg{
    width: 24px;
    height: 24px;
}

.Icon-container-check:hover .Icon-svg{
    fill: green;
}

.Icon-container-delete:hover .Icon-svg{
    fill: red;
}