.TodoSearch {
    background: #f9fbfc;
    border-radius: 2px;
    border: 2px solid #202429;
    margin:  0 24px;
    height: 64px;
    width: calc(100% - 62px);
    font-size: 24px;
    text-align:  center;
    font-family: "Montserrat";
    font-weight: 400;
    color: #1E1E1F;
    box-shadow: 0px 5px 50px rgb(255, 0, 55);
}
.TodoSearch::placeholder {
    color: #A5A5A5;
    font-family: "Montserrat";
    font-weight: 400;
}

.TodoSearch:focus {
    outline-color: #fd0000b2;
}
